.g-date-stub-button {
  position: relative;
  display: inline-block;
  width: 24px;
  height: 24px;
}
.g-date-stub-button_size_xs {
  width: 20px;
  height: 20px;
}
.g-date-stub-button_size_m {
  width: 28px;
  height: 28px;
}
.g-date-stub-button_size_l {
  width: 36px;
  height: 36px;
}
.g-date-stub-button__icon {
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--g-color-text-secondary);
}