.g-date-mobile-calendar {
  position: absolute;
  z-index: 1;
  inset-block-start: 0;
  inset-inline-start: 0;
  box-sizing: border-box;
  min-width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
  opacity: 0;
  border: none;
}
.g-date-mobile-calendar::-webkit-calendar-picker-indicator {
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  min-width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
}